<template>
    <ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <a href="#" class="pagination__link pagination__link--arrow"
            :class="{'pagination__link--disabled':page === 1}"
            @click.prevent="paginate(page - 1, pages)"
            aria-label="Предыдущая страница" >
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" v-bind:key="pageNumber">
            <a href="#" class="pagination__link"
            :class="{'pagination__link--current':pageNumber === page}"
            @click.prevent="paginate(pageNumber, pages)">
              {{pageNumber}}
            </a>
          </li>
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow"
            :class="{'pagination__link--disabled':page === pages}" href="#"
            aria-label="Следующая страница" @click.prevent="paginate(page + 1, pages)">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
      </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page, pages) {
      if ((page !== 0) && (page !== pages + 1)) {
        this.$emit('paginate', page);
      }
    },
  },
};
</script>
